body {
  margin: 0;
  padding: 0;
  font-weight: 400;
  font-size: 16px;
  line-height: normal;
  font-family: "Cera Pro", sans-serif;
  color: #000000;
  background-color: #fff; }

html {
  box-sizing: border-box; }

*,
*::after,
*::before {
  box-sizing: inherit; }

* {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }

a {
  display: inline-block;
  color: inherit;
  text-decoration: none;
  cursor: pointer; }

button {
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer;
  padding: 0; }

ul,
li {
  margin: 0;
  padding: 0;
  list-style: none; }

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0; }

img {
  max-width: 100%;
  height: auto;
  display: flex; }

input {
  width: 100%;
  outline: none; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none; }

input[type="number"] {
  appearance: textfield; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
