/* youtube lazyload container */
.yt-lazyload{
  width:100%;
  max-height: 430px;
  position:relative!important;
  overflow:hidden;
  cursor:pointer;
  -moz-user-select:none;
  -ms-user-select:none;
  user-select:none;
}

/* youtube lazyload container ::before --> to maintain 16/9 ratio & black bg before JS loaded */
.yt-lazyload::before{
  content:'';
  width:100%;
  display:block;
  position:relative;
  padding-top:56.25%; /*16:9 ratio*/
  background-color:#000;
}

/* youtube lazyload wrap */
.yt-lazyload-wrap{
  position:absolute;
  top:0;
  right:0;
  bottom:0;
  left:0;
  padding:inherit;
}

/* youtube lazyload content */
.yt-lazyload-content{
  width:100%;
  height:100%;
  position:relative;
  background-color:#000;
  background-image:var(--yt-lazyload-img);
  background-size:cover;
  background-position:50%;
  background-repeat:no-repeat;
}

/* play-btn */
.yt-lazyload-playbtn{
  display:block;
  position:absolute;
  top:0;
  right:0;
  bottom:0;
  left:0;
  z-index:1;
