/* logo */
.yt-lazyload-logo{
  display:block;
  width:30%;
  min-width:95px;
  min-height:22px;
  max-width:215px;
  padding-top:6.8%;   /* 22.3577236% (logo height/width in %) * 0.3 (equivalent to width:30%) = 6.70731708% rounded up */
